@font-face {
  font-family: "Yoon";
  src: url(./font/KoPubDotumLight.otf) format("truetype");
}

* {
  margin: 0;
  padding: 0;
  font-family: "Yoon";
}

a {
  cursor: pointer;
}

a:hover {
  color:#3B8AF7;
}

.main {
  width: 100%;
  height: 1200px;
  margin: 0 auto;
  background-color: white;
}

.main_header {
  width: 66%;
  height: 70px;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0 auto;
  font-size: 14px;
}

.main_header img {
  width: 20%;
  height: 70px;
}

.main_header_link {
  display: flex;
  flex-direction: row;
  width: 60%;
  height: 100%;
  justify-content: center;
}

.main_header_link a {
  width: 120px;
  text-decoration: none;
  text-align: center;
  padding-top: 30px;
  margin-left: 20px;
  margin-right: 20px;
}

.main_header_members {
  display: flex;
  flex-direction: row;
  width: 20%;
  padding-left: 60px;
  padding-top: 30px;
}

.main_header_members a {
  width: 100px;
  text-decoration: none;
}

.header {
  width: 100%;
  height: 90px;
  padding-top: 30px;
  padding-bottom: 30px;
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  background-color: #3f64c3;
}

.content {
  width: 70%;
  height: 60%;
  display: flex;
  flex-direction: row;
  margin: 0 auto;
}

.content_search {
  width: 20%;
  height: 100%;
  padding: 10px;
}

.content_search_view {
  width: 80%;
  height: 100%;
}

.commons{
  width: 100%;
  height: 200px;
  margin: 0 auto;
}

.usedcar_list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 0 auto;
  justify-content: space-around;
}

.search_list {
  width: 80%;
  height: 600px;
  background-color: aquamarine;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.search_result {
  width: 100px;
  height: 150px;
  background-color: brown;
  margin: 10px;
}

.imgdiv {
  position: relative;
  width: 300px;
  height: 100%;
  overflow: hidden;
  background-color: beige;
}

.innerimg {
  position: relative;
  width: 100%;
  height: 100%;
}

.carinfo {
  width: 100%;
}

.carinfo h2 {
  margin-top: 70px;
  padding-left: 10px;
}

.carinfo a {
  position: relative;
  float: right;
  top: -17%;
  padding-right: 30px;
}

.carinfo_detail {
  display: flex;
  flex-direction: row;
  width: 100%;
  font-size: 10px;
}

.carinfo_detail p {
  padding-right: 10px;
  padding-left: 10px;
  border-right: 1px solid rgb(197, 197, 197);
}

.carinfo_detail p:last-child {
  border: 0;
}

.car_price {
  position: relative;
  float: right;
  top: -17%;
  padding-right: 30px;
}

.pagination_btn {
  position: absolute;
  width: 80%;
  height: 100px;
  top: 227%;
  left: 10%;
  margin: 0 auto;
  text-align: center;
}

.pagination_btn button {
  width: 30px;
  height: 30px;
  border: 0;
  background-color: #3f64c3;
  cursor: pointer;
  margin: 5px;
  text-align: center;
  color: white;
}

.pagination_btn button:hover {
  background-color: rgb(100, 100, 255);
}

.view_detail {
  width: 90%;
  height: 500px;
  background-color: beige;
  margin: 0 auto;
}

.carlist {
  width: 33%;
  margin-top: 40px;
}

.card {
  /* Add shadows to create the "card" effect */
  width: 80%;
  height: 320px;
  margin: 0 auto;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  background-color: white;
  border-radius: 5px;
  padding-bottom: 10px;
}

.card p {
  font-size: 10px;
}

.card img {
  width: 100%;
  height: 60%;
  border-radius: 5px 5px 0 0;
}

/* On mouse-over, add a deeper shadow */
.card:hover {
  box-shadow: 0 12px 24px 0 rgba(0, 0, 0, 0.3);
}

/* Add some padding inside the card container */
.container {
  padding: 2px 16px;
  display: flex;
  flex-direction: column;
}

.container a {
  font-size: 12px;
  padding-top: 10px;
}

.detail {
  display: flex;
  flex-direction: row;
}

.detail p {
  margin-top: 10px;
  margin-bottom: 10px;
  padding-right: 5px;
  padding-left: 5px;
  border-right: 1px solid gray;
}

.detail p:first-child {
  padding-left: 0px;
}

.detail p:last-child {
  border: none;
}

.detail_search {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.inner_box {
  width: 50%;
}

.search_form {
  width: 550px;
}

.search_input {
  width: 75%;
  height: 120%;
  border: 0;
  padding-left: 10px;
}

.search_btn {
  width: 20%;
  height: 120%;
  background-color: #3B8AF7;
  border: 0;
  color: white;
}

.header h2 {
  width: 200px;
  font-size: 1.5rem;
  font-weight: bolder;
  color: #fff;
}

.accordian_show {
  width: 100%;
  display: flex;
  flex-direction: column;
  transition: all 0.2s;
  height: 57px;
  visibility: visible;
  overflow: hidden;
}

.accordian_show input {
  border: 1px solid gray;
  padding-left: 4px;
}

.accordian_show ::placeholder {
  color: black;
  font-size: 8px;
}

.accordian_hide input{
  border: 1px solid gray;
  padding-left: 4px;
}

.accordian_hide ::placeholder{
  color: black;
  font-size: 8px;
}

.accordian_show:nth-child(4) {
  height: 176px;
}

.accordian_show:nth-child(6) {
  height: 25px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.accordian_show:nth-child(6) input {
  width: 40%;
  height: 20px;
}

.accordian_show:nth-child(8) {
  height: 25px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.accordian_show:nth-child(8) input {
  width: 40%;
  height: 20px;
}

.accordian_show:nth-child(10) {
  height: 25px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-bottom: 1px solid gray;
}

.accordian_show:nth-child(10) input {
  width: 40%;
  height: 20px;
}

.accordian_hide {
  width: 100%;
  display: flex;
  flex-direction: column;
  transition: all 0.2s;
  height: 0vh;
  visibility: hidden;
  overflow: hidden;
}

.accordian_hide:nth-child(2n+6) {
  width: 100%;
  display: flex;
  flex-direction: row;
  transition: all 0.2s;
  height: 0vh;
  visibility: hidden;
  overflow: hidden;
  justify-content: space-between;
}

.accordian_hide:nth-child(2n+6) input {
  width: 40%;
  height: 20px;
}

.accordian_show p{
  padding-top: 3px;
}

.accordian_hide p{
  padding-top: 3px;
}

input[type="checkbox"]{
  display: none;
}

input[type="checkbox"] + label{
  display: inline-block;
  width: 16px;
  height: 16px;
  border:1px solid gray;
  position: relative;
}

input[type="checkbox"]:checked + label::after{
  content:'âœ”';
  font-size: 12px;
  width: 16px;
  height: 16px;
  text-align: center;
  position: absolute;
  left: 0;
  top:0;
  color: blue;
}

.detail_search {
  width: 100%;
  margin-top: 30px;
}

.detail_search form {
  width: 100%;
}

.detail_search h3 {
  width: 100%;
  border-top: 1px solid gray;
  padding-top: 5px;
  padding-bottom: 5px;
  font-size: 15px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.detail_search h2 {
  font-size: 21px;
  margin-bottom: 5px;
}

.submit_btn {
  margin-top: 10px;
  width: 100%;
  height: 40px;
  background-color: #3f64c3;
  border: 0;
  color: white;
}

.checkbox_input {
  width: 11px;
  height: 11px;
  margin-top: 4px;
}

.inner_box {
  display: flex;
  flex-direction: row;
  padding-top: 5px;
  padding-right: 5px;
  padding-bottom: 5px;
}

.inner_box div {
  padding-top: 2px;
  padding-left: 5px;
}


.detail_search h3:last-child {
  border-bottom: 1px solid gray;
}

.detail_search label {
  font-size: 11px;
}

.main_card{
  width: 100%;
  height: 200px;
  display: flex;
  flex-direction: row;
  margin-top: 3rem;
  justify-content: space-around;
}

.main_card div{
  width: 28%;
  transition: .3s;
}

.main_card div:hover{
  background-color: #3f64c3;
  color: white;
  transition: .3s;
}

.inner_line{
  background-color: #fff;
  width: 95%;
  margin: 0 auto;
  margin-top: 40px;
  border-top: 1px solid rgb(195, 195, 195);
}

.view_count{
  width: 95%;
  margin: 0 auto;
  padding-top: 20px;
  display: flex;
}

.view_count p{
  padding-left: 10px;
}

.container h6 t{
  color: #3f64c3;
}

